#home {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: space-around;

  section {
    //background-color: rgba(0, 0, 0, 0.7);
    padding-left: 2rem;
    padding-right: 2rem;
    height: 50%;
    width: 20%;
  }

  h1 {
    text-align: center;
    color: #000000;
    font-size: 40px;
  }

  .play-button-container {
    width: 100%;
  }

  a.play-button {
    color: #007bff;
    border-color: #007bff;
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  }

  a.competed-quizzes-button {
    background-color: steelblue;
    border-radius: 30px;
    color: white;
    cursor: pointer;
    padding: 10px;
    text-align: center;
    display: block;
  }

  .auth-container {
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-top: 40px;
  }

  .auth-container .auth-buttons {
    color: white;
    cursor: pointer;
    border-radius: 30px;
    padding: 10px 0;
    text-align: center;
    width: 45%;
    margin: 5px;
  }

  #login-button {
    background-color: darkblue;
  }

  #register-button {
    background-color: red;
  }

  #play-button {
    background-color: green;
  }

  #history-button {
    background-color: #5a9cff;
  }

  #logout-button {
    background-color: red;
  }

  .logout-button-container {
    text-align: center;
    margin: 10px;
  }
  .logout-button {
    border: steelblue solid 1px;
    border-radius: 5px;
  }
}
